html, body {
  display: flex;
  width: 100%;
  height: 100%;
}

body {
  flex: 1;
  flex-direction: column;
  background: linear-gradient(180deg, hsl(235, 16%, 14%) 0%, hsl(236, 21%, 26%) 100%);
}

.bg-stars {
  flex:0.75;
  background-image: url("../img/bg-stars.svg");
}

.bg-hills {
  flex: 0.25;
  background-position: bottom 20%;
  background-image: url("../img/pattern-hills.svg");
}

div {
  display: flex;
}

.body h1 {
  flex: 1;
  justify-content: center;
}

.countdown {
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.day, .hour, .minute, .second {
  width: 200px;
  height: 205px;
  margin: 25px;
  background-color: hsl(234, 17%, 12%);
  flex-direction: column;
  padding-bottom: 5px;
  border-radius: 20px;
  overflow: hidden;
}

.upper {
  flex: 1;
  border-radius: 20px 20px 0 0;
  background-color: hsl(235, 16%, 14%);

  &:before {
    content: "";
    align-self: flex-end;
    justify-self: flex-start;
    background-color: black;
    margin-left: -10px;
    margin-bottom: -10px;
    height: 20px;
    width: 20px;
    border-radius: 10px;
    z-index: 1;
  }

  &:after {

  }
}

.lower {
  flex: 1;
  border-radius: 0 0 20px 20px;
  background-color: hsl(236, 21%, 26%);
}
